var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"detail-info"},[_c('h1',{staticClass:"name"},[_vm._v("为什么商界强人选择双重血液净化")]),_c('div',{staticClass:"tag"},[_c('span',[_vm._v("时间：2019.01.03")]),_c('span',[_vm._v("阅读：208")])]),_c('p',[_vm._v("今年50岁的张女士（化名），是一位实打实的女强人。工作上作为公司CEO带领员工打拼在商界，生活中又有作为人妻人母的一面。虽然说生活事业顺风顺水，但是长期以来的高负荷工作，隔三差五的饭局应酬，也渐渐让这位女强人感觉到了岁月不饶人。")]),_c('p',[_vm._v("一觉醒来第一个反应还是“累”，身体永远都像被疲劳包住无法逃脱。肩膀酸痛手脚冰凉是常态，隔三差五还会闹个头晕头疼。加上年龄关系，50岁的她明显感觉到了衰老正在一步步逼近，少了红润，多了皱纹，“光彩照人”这样的形容词也不在属于自己。")]),_c('p',[_vm._v("每年一次的体检更是给她敲响了警钟，不少指标都呈现偏高倾向，妥妥的加入了动脉硬化糖尿病后备军，这样的结果也让张女士颇有些兵临城下的危机感。仿佛自己不再是那个驰骋疆场的女斗士，已经无法征战四方，唯有尽早褪甲还乡。")]),_c('p',[_vm._v("在一次酒会上，她从朋友那里听说了日本的血液净化疗法，又看到刚做完这个项目的朋友容光焕发，张女士二话不说决定也要给自己如此保养一番。经朋友介绍，她找到了泰璞日本，经过进一步的咨询和商议，张女士来到东京Midtown Clinic接受双重血液净化。")]),_c('p',[_vm._v("在一次酒会上，她从朋友那里听说了日本的血液净化疗法，又看到刚做完这个项目的朋友容光焕发，张女士二话不说决定也要给自己如此保养一番。经朋友介绍，她找到了泰璞日本，经过进一步的咨询和商议，张女士来到东京Midtown Clinic接受双重血液净化。")]),_c('h3',[_vm._v("双重血液净化是什么")]),_c('p',[_vm._v("所谓血液净化，就是利用特殊的过滤膜，将血液中的有害成分（低密度胆固醇，中性脂肪，病毒，炎症物质等等）分离出来，再将去除有害成分之后的干净血液输回体内。")]),_c('div',[_c('img',{attrs:{"src":require("../../assets/img/detail/detail_jinghua_1.png")}}),_c('span',[_vm._v("（血液净化图示）")])]),_c('p',[_vm._v("1次过滤膜（血球分离器）→ 包含了低密度胆固醇，中性脂肪，病毒，炎症物质等成分的血浆 → 排毒之后干净的血浆 → 2次过滤膜（过滤病因物质装置）→ 去除低密度胆固醇，抗炎症效果，排毒效果 → 血球，低密度胆固醇（分子量大），白蛋白等身体必须成分（分子量小）→ 将身体必须成分送回体内")]),_c('h3',[_vm._v("血液净化的功效")]),_c('ul',{staticClass:"ul-1"},[_c('li',[_vm._v("直接去除造成动脉硬化的脂质和炎症细胞等有害物质")]),_c('li',[_vm._v("改善血管内皮功能")]),_c('li',[_vm._v("改善血流，促进新陈代谢")]),_c('li',[_vm._v("抗衰老效果从内而外，肌肤恢复光彩")]),_c('li',[_vm._v("提高免疫功能，预防各类富贵病，生活习惯病及癌症")])]),_c('h3',[_vm._v("Midtown Clinic体验血液净化")]),_c('p',[_vm._v("Midtown Clinic坐落在东京六本木，是一家高档私人诊所，专门进行各类高端体检和包括血液净化在内的私人订制专项治疗。")]),_c('p',[_vm._v("“一点不像诊所，简直就是五星酒店！”，这是张女士在翻译的陪同下走进诊所的第一感觉。安静而庄重的室内空间，让人在不知不觉间放松神经，完全没有“看医生”的紧张感。")]),_c('div',[_c('img',{attrs:{"src":require("../../assets/img/detail/detail_jinghua_2.png")}}),_c('span',[_vm._v("（Midtown Clinic院内环境）")])]),_c('p',[_vm._v("来到这里的第一项，就是细致的问诊。医生一定要和客户有充分的沟通，在完全理解治疗目的效果等内容的前提下才会进行治疗。听着医生简明扼要又清晰易懂的解释，张女士马上就理解了医院谨慎的态度，也对他们的认真负责表示了赞扬。据她说，以往在国内医院做任何治疗都是命令式，让人总有一种不信任感。而在这里，过程透明解释清晰，自然就会信任医院。")]),_c('p',[_vm._v("血液净化治疗大约需要2～3小时，整个过程客户半躺在非常舒适的躺椅上，有专人为客户服务。张女士感慨，不仅护士的技术非常好，而且全程带着微笑，说话也是轻声细语非常温柔，难怪大家都喜欢在日本接受医疗服务。")]),_c('div',[_c('img',{attrs:{"src":require("../../assets/img/detail/detail_jinghua_3.png")}}),_c('span',[_vm._v("（血液净化中）")])]),_c('div',[_c('img',{attrs:{"src":require("../../assets/img/detail/detail_jinghua_4.png")}}),_c('span',[_vm._v("（血液净化仪器）")])]),_c('p',[_vm._v("血液就是经过这个装置得到净化，在经过第二次过滤之后可以清晰的看到，黄色的低密度胆固醇附着在过滤膜上面。当张女士自己看到这个图片时也被震撼到，真没想到可以有如此直接的效果！")]),_c('div',[_c('img',{attrs:{"src":require("../../assets/img/detail/detail_jinghua_5.png")}}),_c('span',[_vm._v("（过滤装置图解）")])]),_c('p',[_vm._v("(治疗前的2次过滤膜（低密度胆固醇过滤装置），治疗后的2次过滤膜（低密度胆固醇过滤装置），黄色低密度胆固醇及炎症物质附着在过滤膜上面）")]),_c('p',[_vm._v("在治疗前红血球形状变形，血液黏稠流动不畅。而治疗后我们可以清楚的看到，红血球不仅恢复了正常的形状，同时血液流动也非常顺畅。张女士激动的说，对于一个医学门外汉来说，再没有什么比这样的图片更直接更易懂。")]),_c('div',[_c('img',{attrs:{"src":require("../../assets/img/detail/detail_jinghua_6.png")}}),_c('span',[_vm._v("（红细胞变化图示）")])]),_c('p',[_vm._v("(治疗前：红血球变形，血液黏稠流动不畅。治疗后：红血球恢复正常形状，血液流动顺畅）")]),_c('div',[_c('img',{attrs:{"src":require("../../assets/img/detail/detail_jinghua_7.png")}}),_c('span',[_vm._v("（检查数据表）")])]),_c('p',[_vm._v("从数值上，也可以清晰看到治疗前后的变化，不少指标都呈现下降。根据医生的介绍，这个双重血液净化治疗作为正规治疗的辅助手段，效果非常好，如果可以重复进行下去，效果还将进一步提高。")]),_c('p',[_vm._v("张女士一直被高血压高血脂等症状困扰，想降低低密度胆固醇等指标却不知如何是好，这次的血液净化一举数得，效果超出她的预期。在走出诊所时，张女士笑着对翻译说，这个血液净化了不得，做了之后会上瘾！过一段时间我还要再来，做得越多效果越好！")])])}]

