var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"detail-info"},[_c('h1',{staticClass:"name"},[_vm._v("中国贵宾在日本东京表参道体验终极美容")]),_c('div',{staticClass:"tag"},[_c('span',[_vm._v("时间：2019.01.03")]),_c('span',[_vm._v("阅读：208")])]),_c('p',[_vm._v("自从在日本做了干细胞美容之后，王女士不仅肤嫩脸润，更好像变成了干细胞代言人，走到哪儿夸到哪儿。用干细胞来做“终极美容”，真的有这么神？咱们听听她怎么说。")]),_c('p',[_vm._v("年过四十的王女士，看着自己脸上一天天变深的鱼尾纹和法令线，那叫一个不开心！再贵的护肤品也只能治表不能治本，整形美容医院虽多却不知哪家靠谱，变美之路太过严峻，为了做到真正意义上的抗衰老返童颜，她开始把视线放在了日本。毕竟日本不仅在医疗美容方面领先全球，同时全民处女座的别称也从侧面反映了日本人做事严谨的风格，王女士决定把自己交给技术和人品都靠谱的日本医生。")]),_c('p',[_vm._v("泰璞日本在接收到王女士的“指令”之后，迅速为她联系了利用干细胞进行再生医学的著名医院----表参道HELENE。这家诊所可谓干细胞治疗的急先锋，始终走在再生医学最前沿。")]),_c('div',[_c('img',{attrs:{"src":require("../../assets/img/detail/detail_ganxibao_1.png")}}),_c('span',[_vm._v("（时尚代表地-表参道）")])]),_c('div',[_c('img',{attrs:{"src":require("../../assets/img/detail/detail_ganxibao_2.png")}}),_c('span',[_vm._v("（表参道HELENE院内环境）")])]),_c('p',[_vm._v("表参道HELENE CLINIC由院长松冈孝明创建于2013年，至今已经有若干家分院，其中的干细胞再生医疗中心坐落在东京的时尚中心表参道。这里的再生医疗首先提取客户自身的皮肤细胞，在培养中心使用先进的3D培养技术让干细胞增殖，培养达到目标数量之后再通过静脉点滴送回体内。")]),_c('h3',[_vm._v("干细胞治疗过程大解析")]),_c('p',[_vm._v("虽然干细胞治疗可以说是世界上最先进的技术，但是整个治疗过程并不复杂，简单说只需要四个步骤。")]),_c('p',[_vm._v("一、初诊")]),_c('p',[_vm._v("没有充分的沟通，就无法产生足够的信任，更没有让客户百分百满意的保障。HELENE CLINIC非常重视和客户的初次见面，花费大量时间了解客户的需求和希望。")]),_c('p',[_vm._v("王女士非常在意脸部皱纹，向医生询问了许多细节，而松冈医生没有一点厌烦的表现，一一详细回答。王女士直言，在国内无论是多有名的医生，也很难能交流得这么深这么透彻，就冲这个态度也让她毫不犹豫的选择这家诊所。")]),_c('p',[_vm._v("二、采集细胞")]),_c('p',[_vm._v("在这个环节要为客户采血，然后进行局部麻醉，从耳后采集大约3mm的皮肤细胞。“局部麻醉就像是牙科麻醉，一瞬间就结束了，整个过程完全没有任何感觉。之后的恢复也非常好，过了一段时间之后我根本找不到采集细胞的痕迹”。直到现在，每当王女士回忆起这些还会非常感慨。")]),_c('div',[_c('img',{attrs:{"src":require("../../assets/img/detail/detail_ganxibao_3.png")}}),_c('span',[_vm._v("（细胞采集）")])]),_c('p',[_vm._v("三、干细胞细胞培养")]),_c('p',[_vm._v("王女士之所以如此信任HELENE CLINIC，还有一个重要的原因就在于这家诊所不是由第三方培养干细胞，而是自带干细胞培养中心。干细胞培养中心有严格的卫生管理系统保证安全无菌环境，条形码管理保证不会出现错用他人干细胞的医疗事故，这些到位的管理都让王女士非常满意。")]),_c('div',[_c('img',{attrs:{"src":require("../../assets/img/detail/detail_ganxibao_4.png")}}),_c('span',[_vm._v("（细胞采集）")])]),_c('p',[_vm._v("四、注入干细胞")]),_c('p',[_vm._v("经过一段时间的培养，干细胞被增殖到上亿甚至更多，这时就可以通过静脉点滴的方法再将干细胞植入体内。")]),_c('p',[_vm._v("从日程说，王女士这次治疗前后共用了10天，到达的第二天在医院采集干细胞之后就开启了短期日本游，几天内走遍各地好不悠闲。一周之后她的干细胞数量到达目标，医生为她重新注入，全程不过半小时，迅速而有条不紊。")]),_c('p',[_vm._v("回国之后没多久，王女士就发现自己脸上的皱纹和下垂都有了明显改观，她对这次的治疗效果非常满意，对HELENE CLINIC的医生和护士也赞不绝口。")]),_c('p',[_vm._v("如果您也对赴日进行干细胞治疗有兴趣，欢迎垂询泰璞日本，联系方式如下：")])])}]

