			<p>身为国内中坚民企的老总，我几乎把所有的精力都放在了工作上。企业发展越来越好，但是我也是越来越忙，从年龄说，40多岁的中年人需要多关心自己身体，所以我决定从体检开始，先了解身体的具体情况再消除健康隐患。</p>

			<p>这次我通过泰普日本预定了东京银座健康院的高端体检，银座健康院是一家高级私人诊所，因为接待马云体检而在国内名声大噪“一检难求”，我也非常期待这个难得的机会。都说日本体检好，都说银座健康院好，我倒要看看，好在哪里高在何处！</p>

			<p>这次我选择了一日体检套餐，从早上10点到下午3点。除了前一天晚上9点以后禁止饮食之外，并不需要做其他特殊准备。</p>

			<h3>健康院初印象</h3>

			<p>银座是人尽皆知的东京最繁华地段之一，到达医院门口我才知道，在这寸土寸金的地方，健康院竟然拥有独立的一栋10层不动产。整栋楼凝重华贵的外观，仿佛也在传递着健康院的与众不同。</p>

			<div>
				<img src="../../assets/img/detail/detail_tiyan_1.png">
				<span>（东京银座健康院）</span>
			</div>

			<p>一进健康院大门，微笑和蔼的工作人员马上鞠躬迎接，确认了姓名和预约时间之后操作电梯恭送我和翻译上楼，就这样开启了这次与众不同的“体检之旅”。</p>

			<h3>医院环境</h3>
			<p>从进门到离开，除了医生，护士以及全程陪同的翻译之外，我没有见到任何人。移动到其他楼层做检查时，有专人负责引路，整个楼层只为一位客人服务。</p>
			<p>独立休息室也保证了良好的私密性，更衣，问诊，等候，休息，都在同一个房间，完全不会被闲杂人等打扰。</p>
			
			<div>
				<img src="../../assets/img/detail/detail_tiyan_2.png">
				<span>（银座健康院院内环境）</span>
			</div>

			<h3>问诊</h3>
			<p>做过体检的人都知道，检查之前都要填写健康状况。这里的护士并不是放下问卷就走人，而是一边询问一边亲自帮我填写了密密麻麻的问卷。不做不知道一做吓一跳，问卷内容细致到极致，从个人病史到家族病史，从日常起居到个人嗜好，甚至连每天喝几杯酒，抽几根烟都要写清楚。</p>
			
			<h3>院长诊查</h3>
			<p>护士问诊结束后没多久，就是健康院院长细井孝之先生亲自为我诊查。这位风度翩翩的绅士医生除了擅长老年医学，抗衰老医学，骨代谢之外，还是日本皇家御医之一。百闻不如一见，谦和的态度，耐心的应对，从体检流程到具体项目，用桌上准备的模型和册子详细解答我的疑问。说实话，我真没想到在没有开始实质性体检之前，就已经有机会和医生有长达半小时的沟通，这种待遇在国内根本无缘享受。</p>

			<div>
				<img src="../../assets/img/detail/detail_tiyan_3.png">
				<span>（院长诊查）</span>
			</div>

			<h3>MRI</h3>
			<p>结束和细井医生的见面后很快有专人引领我前往检查。护士虽然不会中文，但是始终微笑相迎，哪怕说的全是日语，也是看着我的眼睛说，和国内护士那种没有眼神沟通的接触完全不同！这个细节虽小，却让人感觉到无比舒适。</p>
			<p>每一个检查之前，检查技师都要仔细确认注意事项，比如做MRI之前身上是否佩戴金属饰物等等。CT的时间很短，但是做MRI检查时身体会被固定住30分左右，技师一边示范姿势，一边在我身上加盖检查用设备，还不断询问这里紧不紧，那里疼不疼。在国内也做过类似检查，可是真没有被人这么照顾过！</p>

			<h3>CT</h3>
			<p>我在国内做体检最怕的就是胃镜！特别痛苦不说，医生护士那种冷冰冰的态度也让人很不爽。这次就是带着心理阴影来到日本，没想到细井医生的一席话让我在瞬间送了一口气。原来这里可以利用高精度CT检查，达到“假想内窥镜”的效果，3D成像直接“看到”胃的内部。我难以相信自己不用忍受痛苦就可以做检查，这个技术实在太神奇！</p>

			<div>
				<img src="../../assets/img/detail/detail_tiyan_4.png">
				<span>（“虚拟”内窥镜检查）</span>
			</div>

			<p>做检查时我需要服用一包起泡剂，用起泡剂帮助撑起胃，缺点就是特别想打嗝。技师像对孩子一般不断鼓励我：坚持憋住尽量不要打嗝哦！虽然有点好笑，但是心里还蛮温暖的呢。</p>

			<h3>动脉硬化检查</h3>
			<p>在这个环节，我做了四肢血压脉冲波检查和颈动脉B超，除此之外CT检查中也包含了冠状动脉钙化程度，可以全面了解我的动脉硬化风险</p>
			<p>我们在国内体检都会有心电图项目,检查时在双手手腕夹上检测用夹子，而这个四肢血压脉冲波则是在双手双脚分别夹上夹子。通常来说，下肢的血压值高于上肢血压，四肢同时测量的好处在于可以比较上下肢血压的差异，这样就可以更加精确的了解血管硬化程度。说实话，没来这里之前，我从来没有听说过有这样的检查，但是听完医生和检查技师的解释之后，我甚至感觉到了内心的兴奋：这才是我真正需要的检查！</p>
			<p>颈动脉B超时，我自己不但可以看到监视器上的画面，技师还会一边检查一边细致的给我讲解画面。虽然房间内非常暖和，技师还是在检查前给我盖上毯子，在检查结束后亲自给我擦去脖颈上的啫喱，最后又拿出热毛巾让我再度擦拭，最后对着我深深鞠躬并送出检查室，这样的细节真暖心啊！</p>

