			<h3>双重血液净化是什么</h3>
			<p>所谓血液净化，就是利用特殊的过滤膜，将血液中的有害成分（低密度胆固醇，中性脂肪，病毒，炎症物质等等）分离出来，再将去除有害成分之后的干净血液输回体内。</p>

			<div>
